import { useState, useEffect } from 'react'
import axios from 'axios'
import { ROOT_URL } from '../../../actions'

const useCities = (country) => {
  const [state, setCities] = useState({cities:[],citiesLoading:true})

  useEffect(() => {  
    const getCities = async () => {
      console.log('getting cities...')
      const response = await axios.get(`${ROOT_URL}/location/cities/?countries=${country}`, {
        headers: { 'Content-Type': 'application/json'}
      })
      setCities({cities: response.data, citiesLoading:false})
    }
    if (country){
      getCities()
    }
  }, [country])

  return [state.cities, state.citiesLoading]
}

export default useCities