import React, { Component } from "react";
import { connect } from "react-redux";
import { ClipLoader } from "react-spinners";

import { fetchShipperDetails } from "../../actions/shipperDetailsActions";
import { loadMetabaseEmbedUrl } from '../../actions/analyticsActions';

class Analytics extends Component {
  constructor() {
    super();
    this.state = {
      secretKey: '',
      loading: true,
      metabaseIframeUrl: ''
    };
  }

  componentDidMount() {
    this.props.fetchShipperDetails();
  }

  componentDidUpdate() {
    if (this.props.shipperDetails !== undefined && this.state.secretKey.length === 0) {
      this.setState({
        secretKey: this.props.shipperDetails.agent_application_secret_key
      });
      this.props.loadMetabaseEmbedUrl(this.props.shipperDetails.agent_application_secret_key);
    }

    if (this.props.analyticsData !== undefined && this.state.metabaseIframeUrl.length === 0) {
      console.log(this.props.analyticsData.metabase_iframe_url);
      this.setState({
        metabaseIframeUrl: this.props.analyticsData.metabase_iframe_url
      });
    }
  }

  iframeLoaded = () => {
    this.setState({
      loading: false
    });
  }

  render() {
    return (
      <div>
        {
          this.state.loading ?
          <div className="mt-5" style={{ textAlign: "center" }}>
            <div style={{ display: "inline-block" }}>
              <ClipLoader color={"#273272"} loading={true} />
            </div>
          </div>
          :
          null
        }

        {
          this.state.metabaseIframeUrl ?
          <iframe
          src={this.state.metabaseIframeUrl}
          frameBorder="0"
          width="100%"
          height="600"
          allowtransparency="true"
          title="Analytics"
          onLoad={this.iframeLoaded}
          />
          :
          null
        }
      </div>
    );
  }
}

function mapStateToProps({ shipperDetails, analytics }) {
  return ({
    shipperDetails: shipperDetails.shipperDetails,
    analyticsData: analytics.data
  });
}

export default connect(mapStateToProps, {
  fetchShipperDetails,
  loadMetabaseEmbedUrl
})(Analytics);
